import React from 'react';

import BG from '../../assets/images/404/bg.png';
import img404 from '../../assets/images/404/404.png';
import { Link } from 'react-router-dom';

import './index.css';

const NotFound = () => {
  document.addEventListener('mousemove', parallax);

  function parallax(e) {
    document.querySelectorAll('.object').forEach((move) => {
      const moveValue = move.getAttribute('data-value');

      const x = (e.clientX * moveValue) / 250;
      const y = (e.clientY * moveValue) / 250;

      move.style.transform = `translateX(${x}px) translateY(${y}px)`;
    });
  }

  return (
    <section
      id="error_not_found"
      className="absolute h-screen left-0 right-0 top-0 overflow-hidden bg-black"
    >
      <div className="absolute w-full h-full">
        <img
          src={BG}
          data-value="2"
          alt="bg-not-found"
          className="object absolute w-full h-full object-cover"
        />
      </div>
      <div
        className="object absolute h-full w-full grid place-items-center"
        data-value="-2"
      >
        <div className="absolute mx-auto img_notfound">
          <img src={img404} alt="bg-not-found" className="mx-auto img-404" />
        </div>
        <div className="w-max mx-auto mt-96">
          <Link
            to="/"
            className="uppercase bottom-32 border-2 text-white border-white text-sm font-bold py-5 px-14 rounded-radius-10 hover:bg-white hover:text-black"
          >
            Back Home
          </Link>
        </div>
      </div>
    </section>
  );
};

export default NotFound;
